import { Alert, Box, Button, FormHelperText, Grid, InputLabel, OutlinedInput, Stack } from '@mui/material';
import * as Yup from 'yup';
import { Formik, Form } from 'formik';
import { useEffect, useState } from 'react';
import http from '../../../../api/axios';
import IUser from '../../../../interfaces/IUser';

const celRegex = /([0-9]{2,3})?(\([0-9]{2}\))([0-9]{4,5})([0-9]{4})/;

const OverviewData = () => {
  const userLocalStorage = JSON.parse(localStorage.getItem('user') || '{}');
  const jwtValue = userLocalStorage.jwt;
  const idValue: string = userLocalStorage.id;

  const [showSucessAlert, setShowSucessAlert] = useState<boolean>(false);
  const [showFailAlert, setShowFailAlert] = useState<boolean>(false);

  const handleCloseAlert = () => {
    setShowSucessAlert(false);
    setShowFailAlert(false);
  };

  const [initialValues, setInitialValues] = useState<IUser>({
    firstName: '',
    lastName: '',
    email: '',
    cellphone: '',
    address: [],
  });


  useEffect(() => {
    const fetchUserData = async () => {
      try {
        await http.get<IUser>(`/users/${idValue}`, {
    headers: {
      Authorization: `Bearer ${jwtValue}`,
    },
  })
      .then(response => {
        const updatingInitialValues: IUser = response.data;
        setInitialValues(updatingInitialValues);
      })
      .catch(error => {
        console.log(error);
      })
    } catch (error:unknown) {
      console.log(error);
    }
  };
  fetchUserData();
  }, []);


  const yupValidationSchema = Yup.object().shape({
    firstName: Yup.string().max(255).required('Nome Obrigatrio'),
    lastName: Yup.string().max(255).required('Sobrenome Obrigatrio'),
    cellphone: Yup.string().matches(celRegex, 'Este numero no  valido, o formato deveria ser (XX)XXXXXXXXX'),
    email: Yup.string().email('Must be a valid email').max(255).required('Email Obrigatrio'),
  });


  return (
    <>
      <Formik
        initialValues={initialValues}
        validationSchema={yupValidationSchema}
        enableReinitialize={true}
        onSubmit={async (values, { setStatus, setSubmitting }) => {
       
          try {
            setStatus({ success: false });
            setSubmitting(false);
            const response = await http.put(`/users/${idValue}`, JSON.stringify(values), {
              headers: {
                Authorization: `Bearer ${jwtValue}`,
                'Content-Type': 'application/json'
              },
            });
            response;
            setShowSucessAlert(true);
            setShowFailAlert(false);
          } catch (err) {
            console.error(err);
            setStatus({ success: false });
            setSubmitting(false);
            setShowSucessAlert(false);
            setShowFailAlert(true);
          }
        }}

      >
        {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
          <Form noValidate onSubmit={handleSubmit}>
            {showSucessAlert &&
              <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                <Alert onClose={handleCloseAlert} severity="success" sx={{ position:'absolute', top:120,width: '60%' }}>
                  Dados atualizados com sucesso!
                </Alert>
              </Box>
            }
            {showFailAlert &&
              <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                <Alert onClose={handleCloseAlert} severity="error" sx={{ position:'absolute', top:120,width: '60%'}}>
                  Erro: Dados no foram atualizados.
                </Alert>
              </Box>
            }
            <Grid
              container spacing={2} sx={{
                display: 'flex', flexWrap: 'wrap', p: 4, boxSizing: 'border-box',
                '& .MuiTextField-root': { mb: 1 },
              }}>

              <Grid item xs={12} md={12}>
                <Stack spacing={1}>
                  <InputLabel sx={{ fontWeight: 'bold' }}>Nome</InputLabel>
                  <OutlinedInput
                    id='firstName'
                    type='firstName'
                    value={values.firstName}
                    name='firstName'
                    placeholder='Nome'
                    onBlur={handleBlur}
                    onChange={handleChange}
                    error={Boolean(touched.firstName && errors.firstName)}
                  />
                  {touched.firstName && errors.firstName && (
                    <FormHelperText error id="helper-text-firstname-signup">
                      {errors.firstName}
                    </FormHelperText>
                  )}
                </Stack>
              </Grid>

              <Grid item xs={12} md={12}>
                <Stack spacing={1}>
                  <InputLabel sx={{ fontWeight: 'bold' }}>Ultimo Sobrenome</InputLabel>
                  <OutlinedInput
                    id='lastName'
                    type='lastName'
                    value={values.lastName}
                    name='lastName'
                    placeholder='Sobrenome'
                    onBlur={handleBlur}
                    onChange={handleChange}
                    error={Boolean(touched.lastName && errors.lastName)}
                  />
                  {touched.lastName && errors.lastName && (
                    <FormHelperText error id="helper-text-lastname-signup">
                      {errors.lastName}
                    </FormHelperText>
                  )}
                </Stack>
              </Grid>

              <Grid item xs={12} md={12}>
                <Stack spacing={1}>
                  <InputLabel sx={{ fontWeight: 'bold' }}>Celular</InputLabel>
                  <OutlinedInput
                    id='cellphone'
                    type='cellphone'
                    value={values.cellphone}
                    name='cellphone'
                    placeholder='Celular'
                    onBlur={handleBlur}
                    onChange={handleChange}
                    error={Boolean(touched.cellphone && errors.cellphone)}
                  />
                  {touched.cellphone && errors.cellphone && (
                    <FormHelperText error id="helper-text-cellphone-signup">
                      {errors.cellphone}
                    </FormHelperText>
                  )}
                </Stack>
              </Grid>

              <Grid item xs={12} md={12}>
                <Stack spacing={1}>
                  <InputLabel>E-mail*</InputLabel>
                  <OutlinedInput
                    id='email'
                    type='email'
                    value={values.email}
                    name='email'
                    placeholder='email@email.com'
                    onBlur={handleBlur}
                    onChange={handleChange}
                    error={Boolean(touched.email && errors.email)}
                  />
                  {touched.email && errors.email && (
                    <FormHelperText error id="helper-text-email-signup">
                      {errors.email}
                    </FormHelperText>
                  )}
                </Stack>
              </Grid>

              {/* <Grid item xs={12} md={12} sx={{ display: 'flex', justifyContent: 'center', mt: 2, mb: 1 }}>
                <Typography variant='h5'>Endereo</Typography>
              </Grid>


              <Grid item xs={12} md={12}>
                <Stack spacing={1}>
                  <InputLabel>Rua</InputLabel>
                  <OutlinedInput
                    id='addressStreet'
                    type='addressStreet'
                    value={values.addressStreet}
                    name='addressStreet'
                    placeholder='Rua Alameda Avenida'
                    onBlur={handleBlur}
                    onChange={handleChange}
                    error={Boolean(touched.addressStreet && errors.addressStreet)}
                  />
                  {touched.addressStreet && errors.addressStreet && (
                    <FormHelperText error id="helper-text-addressStreet-signup">
                      {errors.addressStreet}
                    </FormHelperText>
                  )}
                </Stack>
              </Grid>
               {errors.submit && (
                <Grid item xs={12}>
                  <FormHelperText error>{errors.submit}</FormHelperText>
                </Grid>
              )}
              
              */}


              <Grid item xs={12}>
                <Button
                  disableElevation
                  disabled={isSubmitting}
                  fullWidth
                  size="large"
                  type="submit"
                  variant="contained"
                  color="primary"
                >
                  Atualizar Informaes
                </Button>
              </Grid>
            </Grid>
          </Form>
        )}
      </Formik>
    </>
  )
}

export default OverviewData;